<template>
	<div class="TicketDetails">
		<div class="DetailsHeader mb-4">
			<b-button variant="flat-danger" @click="GoBack">
				<feather-icon icon="ChevronLeftIcon" class="mr-50" />
				<span class="align-middle">返回工单列表</span>
			</b-button>
			<h5>工单详情 - # {{ ID }}</h5>
		</div>
		<b-card v-for="(comment, index) in dialogs.message" :key="index" class="col-lg-6 mx-auto">
			<b-media no-body>
				<b-media-aside>
					<b-avatar
						:src="
							comment.is_me
								? require('@/assets/images/avatars/13-small.png')
								: 'https://cdn.jsdelivr.net/gh/piegun/01@4685640aef7a4e865b073dcc3679304886ef86c9/2021/7/24/13/5260628.png'
						"
						size="38"
					/>
				</b-media-aside>
				<b-media-body>
					<h6 class="font-weight-bolder mb-25">
						{{ comment.is_me ? '我' : '管理员' }}
					</h6>
					<b-card-text>{{ timestampToTime(comment.updated_at) }}</b-card-text>
					<b-card-text>
						{{ comment.message }}
					</b-card-text>
				</b-media-body>
			</b-media>
		</b-card>
		<b-card class="col-lg-6 mx-auto mt-2">
			<b-input-group>
				<b-form-input v-model="reply_txt" :placeholder="dialogs.status == 0 ? '输入回复内容' : '工单已关闭，请新建工单'" />
				<b-input-group-append>
					<b-button @click="reply()" variant="outline-primary" :disabled="dialogs.status != 0 || isPosting">
						回复工单
					</b-button>
				</b-input-group-append>
			</b-input-group>
		</b-card>
	</div>
</template>

<script>
import {
	BButton,
	BCard,
	BMedia,
	BMediaBody,
	BCardText,
	BMediaAside,
	BAvatar,
	BInputGroup,
	BFormInput,
	BInputGroupAppend,
	BInputGroupPrepend
} from 'bootstrap-vue'

export default {
	components: {
		BButton,
		BCard,
		BMedia,
		BMediaBody,
		BCardText,
		BMediaAside,
		BAvatar,
		BInputGroup,
		BFormInput,
		BInputGroupAppend,
		BInputGroupPrepend
	},
	data() {
		return {
			ID: '',
			updateThread: null,
			dialogs: {},
			reply_txt: '',
			isPosting: false
		}
	},
	mounted() {
		this.ID = this.$route.params.id
		this.getTicketContent()
		this.updateThread = setInterval(() => {
			this.getTicketContent()
		}, 10000)
	},
	methods: {
		GoBack() {
			this.$router.push({ name: 'ticket' })
		},
		reply() {
			let data = {
				id: this.$route.params.id,
				message: this.reply_txt
			}
			this.isPosting = true
			this.$http({
				url: '/api/v1/user/ticket/reply',
				method: 'POST',
				data: data
			})
				.then(res => {
					res = res.data.data

					if (res == true) {
						//   this.$store.commit("setUserSub", res);
						this.reply_txt = ''
						this.getTicketContent()
					}
					this.isPosting = false
				})
				.catch(err => {
					clearInterval(this.updateThread)
					msg(err.response.data.message)
					this.isPosting = false
				})
		},
		timestampToTime(timestamp) {
			var date = new Date(timestamp * 1000) //时间戳为10位需*1000，时间戳为13位的话不需乘1000
			var Y = date.getFullYear() + '-'
			var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-'
			var D = (date.getDate() < 10 ? '0' + date.getDate() : date.getDate()) + ' '
			var h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':'
			var m = (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()) + ':'
			var s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds()
			return Y + M + D + h + m + s
		},
		getTicketContent() {
			this.$http({
				url: '/api/v1/user/ticket/fetch?id=' + this.$route.params.id,
				method: 'GET'
			})
				.then(res => {
					res = res.data

					if (res) {
						//   this.$store.commit("setUserSub", res);
						this.dialogs = res.data
					}
					this.isPosting = false
				})
				.catch(err => {
					clearInterval(this.updateThread)
					msg(err.response.data.message)
					this.isPosting = false
				})
		}
	}
}
</script>

<style lang="scss">
.TicketDetails {
	.DetailsHeader {
		display: flex;
		align-items: center;
		* {
			margin: 0;
		}
	}
	.card {
		margin-bottom: 0.2rem !important;
	}
}
</style>
